.parts-links {
  .row {
    @include xs {
      margin-left: -5px;
      margin-right: -5px; } }
  [class*="col-"] {
    @include xs {
      padding-left: 5px;
      padding-right: 5px; } } }

.parts-link {
  @include flex(flex-start, center);
  width: 100%;
  border: 1px solid $c-border;
  padding: 10px 12px;
  padding-left: 20px;
  padding-right: 30px;
  margin-bottom: 18px;
  position: relative;
  @include tr(color .3s, border-color .3s);
  @include hover {
    border-color: $c-accent;
    color: $c-accent; }
  @include before {
    display: inline-block;
    pointer-events: none;
    top: 50%;
    right: 14px;
    @include icon-arrow(8, 8, 1, currentColor, -135);
    transform: translate(0, -50%) rotate(-135deg);
    position: absolute; }
  @include md {
    padding-left: 10px;
    margin-bottom: 10px; }
  @include xs {
    padding-top: 14px;
    padding-bottom: 14px;
    margin-bottom: 5px; }
  &__img {
    margin-right: 35px;
    width: 35px;
    display: block;
    font-size: 0;
    @include md {
      margin-right: 15px; }
    @include xs {
      width: 20px;
      height: 20px;
      margin-right: 5px; } }
  &__title {
    max-width: calc(100% - 65px);
    @include text-overflow;
    display: inline-block;
    @include md {
      max-width: calc(100% - 35px); }
    @include xs {
      max-width: calc(100% - 25px); } } }



.parts {
  .row {
    @include sm {
      margin-left: -10px;
      margin-right: -10px; }
    @include xs {
      margin-left: -5px;
      margin-right: -5px; } }
  [class*="col-"] {
    @include sm {
      padding-left: 10px;
      padding-right: 10px; }
    @include xs {
      padding-left: 5px;
      padding-right: 5px; } } }

.part {
  $this: &;
  @include cover;
  // background-size: 150%
  background-size: contain;
  background-color: #fff;
  background-position: 100% 20%;
  position: relative;
  width: 100%;
  @include flex(flex-end, normal, column);
  border: 1px solid  $c-border;
  padding-top: 100%;
  height: calc(100% - 30px);
  margin-bottom: 30px;
  @include tr(box-shadow .3s);
  @include hover {
    box-shadow: 0px 0px 12.92px 4.08px rgba(0, 0, 1, 0.3);
    #{$this}__btn {
      overflow: visible;
      max-height: 45px;
      padding-top: 15px;
      padding-bottom: 15px; }
    #{$this}__title {
      transform: translate(0, -30px);
      @include sm {
        transform: translate(0, 0); } } }
  &__title {
    padding-left: 18px;
    padding-right: 18px;
    position: absolute;
    bottom: 30px;
    left: 0;
    @include tr(transform .3s);
    @include sm {
      font-size: 12px;
      bottom: 20px;
      .h5, h5 {
        font-size: 12px; } }
    @include xs {
      bottom: 15px;
      font-size: 11px;
      .h5, h5 {
        font-size: 11px; } } }
  &__btn {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    max-height: 0;
    overflow: hidden;
    padding-top: 0;
    padding-bottom: 0;
    @include tr(max-height .3s, padding .3s);
    @include sm {
      display: none; } }

  &--lg {
    padding-top: 0;
    @include cover;
    @include xs {
      padding-top: 100%; }
    #{$this}__title {
      color: #fff; } } }
