.select {
  position: relative;
  width: 100%;
  select {
    opacity: 0;
    visibility: hidden;
    position: absolute; }
  &--number {
    position: relative;
    @include after {
      display: inline-block;
      @include icon-arrow(7, 7, 1, $c-text, -45);
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translate(0, -50%) rotate(-45deg);
      pointer-events: none; }
    select {
      opacity: 1;
      visibility: visible;
      position: static;
      appearance: none;
      width: 100%;
      @extend .custom-select__opener;
      @include after {
        display: none; } }
    &::before {
      content: attr(data-number);
      @include inline-flex(center, center);
      width: 26px;
      height: 26px;
      background-color: $c-accent;
      border-radius: 50%;
      color: #fff;
      font-size: 9px;
      @include light;
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(-50%, -50%);
      z-index: 1; } }
  &--sm {
    color: #a5a5a5;
    font-size: 15px;
    label {
      font-size: inherit;
      display: block;
      margin-bottom: 3px;
      color: $c-text;
      sup {
        color: #fbcf3a;
        font-size: inherit;
        display: inline-block;
        margin-left: .2em; } }
    .custom-select {
      font-size: 14px;
      &__opener {
        padding: 10px 15px;
        color: inherit; }
      &__panel-item {
        padding-left: 15px;
        padding-right: 15px; }
      &__option {
        padding-left: 15px;
        padding-right: 15px;
        padding-top: 5px;
        padding-bottom: 5px; } } } }

.custom-select {
  $this: &;
  @include light;
  font-size: 15px;
  position: relative;
  cursor: pointer;
  &.is-open {
    #{$this}__panel {
      visibility: visible;
      opacity: 1; } }
  &[data-type="with_input"] {
    #{$this}__panel {
      padding-top: 40px; } }

  &__opener {
    padding-top: 14px;
    padding-bottom: 14px;
    padding-right: 20px;
    padding-left: 18px;
    border: 1px solid  $c-border;
    position: relative;

    @include text-overflow;
    @include after {
      display: inline-block;
      @include icon-arrow(7, 7, 1, $c-text, -45);
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translate(0, -50%) rotate(-45deg);
      pointer-events: none; }
    @include sm {
      padding-top: 10px;
      padding-bottom: 10px; } }
  &__panel {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: #fff;
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    border: 1px solid  $c-border;
    max-height: 270px;
 }    // padding-top: 40px
  &__option {
    padding-right: 20px;
    padding-left: 18px;
    padding-top: 15px;
    padding-bottom: 15px;
    @include tr(background-color .3s, color .3s);
    cursor: pointer;
    @include hover {
      background-color: $c-accent;
      color: #fff; }
    @include sm {
      padding-top: 5px;
      padding-bottom: 5px; }
    &[data-value="placeholder"] {
      display: none; } }
  &__panel-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 5px;
    padding-left: 18px;
    padding-right: 20px;
    input {
      border: 1px solid  $c-border;
      font-family: inherit;
      font-size: inherit;
      padding: 5px 10px;
      padding-left: 30px;
      color: inherit;
      width: 100%; }
    .icon {
      position: absolute;
      fill: currentColor;
      top: 12px;
      left: 25px; } }
  &__options {
    overflow: hidden;
    position: relative;
    max-height: 200px; } }

.custom-select {
  .ps {
    &:hover {
      .ps {
        &__rail-y {
          opacity: 1; } } }
    &__rail-y {
      opacity: 1;
      background-color: #e7e7e7; }
    &__thumb-y {
      width: 100%;
      right: 0;
      border-radius: 0; } } }
